@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&family=Ubuntu:wght@400;500;700&display=swap);
* {
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
html{
  scroll-behavior: smooth;
  overflow-x: hidden;
}

/* custom scroll bar */
::-webkit-scrollbar {
  width: 0px;
}
::-webkit-scrollbar-track {
  background: #f1f1f1;
}
::-webkit-scrollbar-thumb {
  background: #888;
}

::-webkit-scrollbar-thumb:hover {
  background: #555;
}
.navbar {
  position: fixed;
  width: 100%;
  height: 80px;
  transition: all 0.5s ease;
  z-index: 999;
}
.navbar.active {
  position: fixed;
  width: 100%;
  height: 60px;
  background: #202020;
  transition: all 0.5s ease;
}
.nav-content {
  width: 95%;
  height: 100%;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.navbar .logo a {
  text-decoration: none;
  font-size: 25px;
  color: #202020;
}
.navbar .menu-items {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
}
.navbar .menu-items .nav-items {
  list-style: none;
  margin: 0px 10px;
}
.navbar .menu-items .nav-items a {
  text-decoration: none;
  color: #202020;
}
.navbar.active .logo a {
  color: #f1f1f1;
}
.navbar.active .menu-items .nav-items a {
  text-decoration: none;
  color: #f1f1f1;
}


.banner {
  width: 100%;
  height: 100vh;
  background: #f0f2f5;
  background-image: url(/static/media/background.5b53f3d2.png);
  background-size: cover;
  background-attachment: fixed;
  display: flex;
  align-items: center;
}

.banner-content {
  margin-left: 40px;
}
.banner-content .text-1 {
  font-size: 30px;
}
.banner-content .text-2 {
  font-size: 60px;
  font-weight: bold;
}
.banner-content .text-3 {
  font-size: 30px;
  display: flex;
}
.banner-content .text-3 .text-3-content {
  font-weight: 500;
}

.about {
  width: 100%;
  min-height: 100vh;
  background: #f1f1f1;
}
.about-wrapper {
  padding-top: 60px;
}
.about .title::after {
  content: "Quien soy";
  background-color: #f1f1f1;
}
.about-content {
  width: 90%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex: 1 1;
  /* flex-wrap: wrap; */
}
.about-content .column-left {
  width: 500px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.about-content .column-left img {
  width: 325px;

  border-radius: 5px;
}
.about-content .column-right {
  width: 60%;
  height: 100%;
  /* margin: 0px 10px 0px 0px; */
  text-align: justify;
  /* display: flex;
  align-items: center;
  flex-direction: column; */
}
.about-content .column-right span {
  font-weight: bold;
}

.about-content .column-right .cv {
  text-decoration: none;
  color: #f0f2f5;
  padding: 10px 15px;
  margin: 5px;
  background: crimson;
  border-radius: 5px;
  border: 2px solid transparent;
  transition: 0.4s;
}

.about-content .column-right .cv:hover {
  color: crimson;
  border: 2px solid crimson;
  background: transparent;
}

.about-content .column-right .cv-lac {
  text-decoration: none;
  color: #f0f2f5;
  padding: 10px 15px;
  margin: 5px;
  background: #4e739b;
  border-radius: 5px;
  border: 2px solid transparent;
  transition: 0.4s;
}

.about-content .column-right .cv-lac:hover {
  color: #4e739b;
  border: 2px solid #4e739b;
  background: transparent;
}

